import React from 'react'
import { useState } from 'react'
import swal from 'sweetalert';
function Borrow({ addData }) {
    const [input, setInput] = useState({ personalId: '', role: '',organ: '',amount: '' ,duration: '', detail: '' });
    const handleSubmit = (e) => {
        e.preventDefault();
        swal({
            title: "กรุณาตรวจสอบข้อมูลอีกครั้ง ?",
            text: "โปรดตรวจสอบข้อมูล ก่อนยืนยันอีกครั้ง",
            icon: "warning",
            buttons: true,
            dangerMode: true,
        })
            .then((check) => {
                if (check) {
                    swal("ระบบได้ทำการส่งข้อมูลเรียบร้อยแล้ว", {
                        icon: "success",
                    }).then(addData(input));
                } else {
                    swal("ระบบไม่ได้ทำการส่งข้อมูลดังกล่าว!");
                }
            });
    }

    const onChange = (e) => {
        e.preventDefault();
        const { id, value } = e.target;
        console.log(value)
        setInput({ ...input, [id]: value });
    }

    return (
        <div className="card shadow p-3 ">
            <div className="card-header h5">Requested Form</div>
            <br />
            <div className="card-body">
                <form onSubmit={handleSubmit} >
                    <div className="form-row">
                        <div className="form-group col-md-4">
                            <label htmlFor="personalId">Personal ID</label>
                            <input type="text" className="form-control thaifont" id="personalId" value={input.personalId} onChange={onChange} placeholder="6109610011" required/>
                        </div>
                        <div className="form-group col-md-3">
                            <label htmlFor="role">Role</label>
                            <input type="text" className="form-control thaifont" id="role" value={input.role} onChange={onChange} placeholder ="Student" required/>
                        </div>
                        <div className="form-group col-md-3">
                            <label htmlFor="organ">Organization</label>
                            <input type="text" className="form-control thaifont" id="organ" value={input.organ} onChange={onChange} placeholder="Thammasat University" required/>
                        </div>
                        <div className="form-group col-md-2">
                            <label htmlFor="amount">Amount</label>
                            <input type="text" className="form-control thaifont" id="amount" value={input.amount} onChange={onChange} placeholder="40" required/>
                        </div>
                    </div>
                    <div className="form-row">
                        <div className="form-group col-md-8">
                            <label htmlFor="detail">Detail</label>
                            <input type="text" className="form-control thaifont" id="detail" value={input.detail} onChange={onChange} placeholder="ยืมไปเพื่อใช้ในการใช้ประกอบการเรียนการสอน" required/>
                        </div>
                    </div>
                    <div className="form-row">
                        <div className="form-group col-md-12">
                            <label htmlFor="duration">Approximately Duration</label>
                            <input type="range" className="form-control-range bg-success" id="duration" min="0" max="24" step="0.5" value={input.duration} onChange={onChange} defaultValue='0' required/>
                            <small id="durationValue" className="form-text text-muted">Duration time : {input.duration} hours.</small>
                        </div>
                    </div>
                    <input type="submit" disabled={false}
                        className="btn btn-primary thaifont w-100" style={{ "fontSize": "20px" }} value="ทำการ Check in เพื่อยืมครุภัณฑ์" />
                </form>
            </div>
        </div>
    )
}
export default Borrow;